.bold-text {
  font-weight: bold;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  background-color: white;
}

.form-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
}

.form-container select, .form-container input{
  width: 50%;
  padding: 8px;
  border-style: solid;
  border-color: hsl(0, 0%, 80%);
  border-width: 1px;
  min-height: 38px;
  padding: 2px 8px;
  font-size: 16px;
}

.form-container input {
  padding: 15px;
}

.form-container button {
  width: 50%;
  padding: 10px;
}

td form{
  text-align: center;
  padding: 10px;
}

input, select {
  padding: 5px;
  border-radius: 4px;
  font-size: 16px;
  border: 1px solid hsl(0, 0%, 80%);
}

.login-form input:focus, .login-form button:focus {
  outline: none; /* Odstraní výchozí obrys */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); /* Přidá jemný stín pro zvýraznění */
}

input:focus, select:focus {
  outline: none; /* Odstraní výchozí obrys */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); /* Přidá jemný stín pro zvýraznění */
}

.nav-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-list li {
  margin: 0 10px;
}

.nav-right {
  margin-left: auto;
}

.center-text {
  text-align: center;
}

.public-title {
  margin: 0;
}

.table-container {
  overflow-x: auto;
  overflow-y: auto;
}

.evidence-table th, .evidence-table td {
  padding: 4px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.evidence-table th {
    font-weight: normal;
    text-transform: uppercase;
    color: #00141e;
    font-family: sans-serif;
}

.evidence-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 15px;
  text-align: left;
  overflow-x: auto;
}

.evidence-table th {
  background-color: #f4f4f4;
}

.evidence-table td:nth-of-type(4) {
  background-color: #ffe5e5;
  font-weight: 500;
  border-left: 2px solid #ffbdbd;
  border-right: 2px solid #ffbdbd;
}

.evidence-table td:nth-of-type(5) {
  background-color: #e5ffe5;
  font-weight: 500;
  border-left: 2px solid #abff9a;
  border-right: 2px solid #abff9a;
}

.evidence-table td:nth-of-type(6) {
  background-color: #fffde5;
  font-weight: 500;
  border-left: 2px solid #fffd9c;
  border-right: 2px solid #fffd9c;
}

.evidence-table-penalties th, .evidence-table-penalties td {
  padding: 5px;
  text-align: left;
  border: 1px solid #ddd;
}

.evidence-table-penalties {
  width: 100%;
  border-collapse: collapse;
  font-size: 15px;
  text-align: left;
  overflow-x: auto;
}

.evidence-table-penalties th {
  background-color: #f4f4f4;
}

.evidence-table-penalties tr:nth-of-type(even) {
  background-color: #f9f9f9;
}

.evidence-table-penalties tr:hover {
  background-color: #f1f1f1;
}

.pokuta-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px; /* Zmenšení textu ve sloupci Pokuty */
}

.pokuta-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
}

.icon {
  margin: 5px;
  font-size: 16px;
  cursor: pointer;
}

svg.icon.upload-icon {
  color: royalblue;
}

.edit-icon {
  color: #2a9d8f;
}

.delete-icon {
  color: #e76f51;
}

.edit-button, .add-button, .save-button {
  padding: 5px 10px;
  border: none;
  background-color: #ED1B26;
  color: white;
  cursor: pointer;
  font-size: 16px;
  border-radius: 4px;
  transition: background-color 0.3s;
  font-weight: 600;
}

.add-player-button {
  padding: 5px 10px;
  border: none;
  background-color: #ED1B26;
  color: white;
  cursor: pointer;
  font-size: 16px;
  border-radius: 4px;
  transition: background-color 0.3s;
  margin-left: 10px;
  font-weight: 600;
}

.cancel-button {
  padding: 5px 10px;
  border: none;
  background-color: #ccc;
  color: black;
  cursor: pointer;
  font-size: 16px;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.cancel-button:hover {
  background-color: #bbb;
}

.edit-button:hover, .add-button:hover, .save-button:hover{
  background-color: darkred;
}

.icon.info-icon {
  color: #ED1B26;
  cursor: pointer;
  margin-left: 5px;
}

.icon.info-icon:hover {
  color: darkred;
}


.overlay {
  background-color: rgba(0, 0, 0, 0.75);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: white;
  padding: 20px;
  border-radius: 10px;
  width: 400px;
  max-width: 80%;
  max-height: 80%;
  overflow-y: auto;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.modal h2 {
  margin: 0;
  text-align: center;
}

.modal ul {
  list-style-type: none;
  padding: 0;
}

.modal ul li {
  font-size: 15px;
  margin: 8px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal ul li .index {
  font-weight: bold;
  margin-right: 8px;
}

.modal ul li small {
  margin-left: auto;
  text-align: right;
  color: grey;
}

.modal ul li .icon.delete-icon {
  flex-shrink: 0;
  font-size: 16px;
  margin-left: 8px;
}

.modal button.modal-close-button {
  display: block;
  margin: 20px auto 0 auto;
  padding: 10px 20px;
  background-color: #ED1B26;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.modal button.modal-close-button:hover {
  background-color: darkred;
}

.show-button {
  background-color: #ED1B26;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  min-width: 35px;
  cursor: pointer;
  font-size: 12px;
  transition: background-color 0.2s;
  font-family: "Fugaz one";
}

.show-button:hover {
  background-color: darkred;
}

.nav-list {
  list-style: none;
  display: flex;
  align-items: center;
  width: 100%;
}

.logo-container {
  flex: 1;
}

.nav-buttons {
